<template>
  <div class="exam_card">
    <!---IMAGE--->
    <img
      class="img-fluid"
      src="https://img2.arabpng.com/20180419/hkw/kisspng-ssc-mts-exam-test-computer-icons-educational-entra-test-paper-5ad919071997b8.5830873915241771591048.jpg"
    />
    <div class="card_title">
      العام الدراسي
      <p>{{ exam.year }}/{{ exam.year + 1 }}</p>
    </div>
    <div class="card_title">{{ exam.stage | stage }}</div>
    <div v-if="exam.unit" class="card_title">{{ exam.unit | section(exam.stage) }}</div>
    <div v-if="exam.name" class="card_title">{{ exam.name }}</div>
    <div
      v-if="exam.model"
      class="card_subtitle"
      style="font-size: 22px; font-weight: bold"
    >
      <!-- Number {{ exam.number }} ({{ exam.model }}) -->
    </div>
    <h5 :style="{ color: isActive() ? 'green' : 'red' }" class="text-center">
      {{ isActive() ? "مفعل" : "غير مفعل" }}
    </h5>
    <hr />
    <div class="card-actions">
      <i class="fa fa-edit" style="color: #146485" @click="() => editExam(exam._id)"> </i>

      <button style="color: #444" @click="() => solveExam(exam._id)">Solve</button>

      <i
        class="fa fa-remove"
        style="color: #cb6d6d"
        @click="
          () => {
            deleteExam(exam._id);
          }
        "
      >
      </i>
      <button
        style="color: #444"
        @click="$router.push(`/dashboard/unit-answers/${exam._id}`)"
      >
        الأجابات
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ExamCard",
  props: ["exam", "editExam", "solveExam", "deleteExam"],
  data: () => {
    return {};
  },
  methods: {
    isActive() {
      return new Date().getTime() > new Date(this.exam.publishDate).getTime();
    },
  },
};
</script>

