<template>
  <div>
    <h2 class="page-title">الأختبارات</h2>
    <div class="cont">
      <div class="filters">
        <div class="filter form-group">
          <label>المرحلة التعليمية</label>
          <select
            @change="
              (e) => {
                this.getUnits(e);
                filterExams();
              }
            "
            class="form-control"
            v-model="filter.stage"
          >
            <option
              v-for="stage in $store.getters.stages"
              :key="stage.value"
              :value="stage.value"
            >
              {{ stage.text }}
            </option>
          </select>
        </div>

        <!--------------------------UNIT----------------------------->
        <div class="filter form-group">
          <label>الوحدة</label>
          <select @change="filterExams" class="form-control" v-model="filter.unit">
            <option
              v-for="unit in units"
              :key="unit.number"
              selected
              :value="unit.number"
            >
              {{ unit.number | section(filter.stage) }}
            </option>
          </select>
        </div>
        <div class="filter form-group">
          <label>العام الدراسي</label>
          <select @change="filterExams" class="form-control" v-model="filter.year">
            <option v-for="y in years" :key="y" :value="y">{{ y }}/{{ y + 1 }}</option>
          </select>
        </div>

        <div class="filter form-group">
          <label>مفعل أم مجدول</label>
          <select @change="filterExams" class="form-control" v-model="filter.active">
            <option :value="true">مفعل</option>
            <option :value="false">مجدول</option>
            <option value="all">all</option>
          </select>
        </div>

        <hr />
      </div>
      <!-- <div class="number">
      <label>تجميع درجات الامتحانات ما عدا</label>
      <input @keydown="setExamsOut" class="form-control" type="text" v-model="outExams" />
    </div> -->
      <br /><br />
      <h3 class="text-center">الامتحانات</h3>
      <hr />
      <div v-if="loading" class="center">
        <loading />
      </div>
      <div v-if="exams.length == 0 && !loading" class="center">
        <h3>لا يوجد عناصر بعد</h3>
      </div>
      <div class="container card card-body">
        <div class="row">
          <div class="col-md-3 p-2" v-for="(exam, e) in exams" :key="e">
            <exam-card
              :exam="exam"
              :editExam="editExam"
              :solveExam="solveExam"
              :deleteExam="deleteExam"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import ExamCard from "./exam-card";
export default {
  components: {
    ExamCard,
  },

  created() {
    this.loading = true;
    this.filter.year = new Date().getFullYear();
    this.getUnits();
    const years = [];
    this.filterExams();
    for (let y = new Date().getFullYear(); y >= 2020; y--) {
      years.push(y);
    }
    this.years = years;
  },
  data() {
    return {
      exams: [],
      modal: false,
      examId: "",
      loading: false,
      years: [],
      filter: {
        stage: "6",
        year: 2021,
        // number: 0,
        active: "all",
        unit: 1,
      },
      solve: false,
      currentExam: null,
      solutionModel: null,
      units: [],
    };
  },
  methods: {
    editExam(id) {
      this.$router.push("/dashboard/edit-unit-exam/" + id);
    },

    solveExam(id) {
      this.solve = false;
      this.$router.push("/dashboard/unit-exam-solutionModel/" + id);
      setTimeout(() => {
        this.solve = true;
      }, 200);
      // this.$refs.modal_button.click();
    },
    filterExams() {
      this.loading = true;
      axios.post("/filterUnitsExams", this.filter).then((res) => {
        this.exams = res.data.exams;
        this.loading = false;
      });
    },
    deleteExam(id) {
      const confirmed = confirm("هل تريد مسح الامتحان نهائيا ؟");
      if (confirmed) {
        axios.delete("/deleteUnitExam/" + id).then((res) => {
          if (res.data.err)
            return this.$store.dispatch("writemessage", "لم يتم المسح حاول مرة أخرى");
          this.exams.splice(
            this.exams.findIndex((exam) => exam._id == id),
            1
          );
          this.modal = false;
        });
      }
    },
    getUnits(e) {
      const stage = e?.target?.value || "6";
      this.units = this.$store.getters.sections.filter((unit) => unit.stage == stage);
    },
  },
};
</script>

