  margin-top: 30px;
  padding: 22px;
  @media (max-width: 600px) {
    padding: 2px;
  }
}
.filters {
  .filter {
    direction: rtl;
    text-align: right;
  }
}
.exams {
  button {
    width: 98%;
    padding: 10px 2px;
    @media (max-width: 600px) {
      font-size: 0.8rem;
    }
  }
}
</style>
